.form {
    width: 320px;
    margin-left: 25px;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    gap: 8px;
  }
  
  .button {
    height: 30px;
    width: 130px;
    cursor: pointer;
    background-color: transparent;
    font-size: 16px;
    border-radius: 12px;
    border: 1px solid rgb(117, 40, 120);
    transition-duration: 0.4s;
  
    &:hover {
      background-color: rgb(117, 40, 120); 
      color: white;
      box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19)
    }
  }
  